html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0; }

.bottom_svg {
  right: calc(12vw - 2px);
  left: auto; }
  @media only screen and (max-width: 980px) {
    .bottom_svg {
      right: calc(50vw - 2px); } }

.bg_svg_top {
  right: 0;
  left: auto; }

.banner_text {
  padding-inline-start: 10vw; }
  @media only screen and (max-width: 980px) {
    .banner_text {
      padding-inline-start: calc(2.5vw + 15px); } }
  .banner_text h1 {
    transform: translateX(-8rem); }

.about_wrap .img_2 {
  right: 0;
  left: auto; }
  .about_wrap .img_2::before {
    left: -1rem;
    right: auto; }

.page-banner .social_wrap {
  left: 1rem;
  right: auto; }

.breadcrumb_wrap {
  transform: translateX(-8rem); }
  .breadcrumb_wrap ul li {
    font-size: 1.2rem; }
    @media only screen and (max-width: 980px) {
      .breadcrumb_wrap ul li {
        font-size: 1.35rem; } }
    .breadcrumb_wrap ul li.current {
      font-weight: 100; }
  .breadcrumb_wrap a {
    font-weight: 100; }

.obj_item .s_bg {
  right: 0;
  left: auto; }

html[dir="rtl"] .member_bg_wrap .mb_item .title_ {
  font-size: clamp(1.1rem, 9vw, 1.5rem); }

.article_wrap .article-title > * {
  transform: scale(1.2) translateX(-6rem) skewX(-20deg); }

.tree_wrap .head_box {
  font-size: clamp(1rem, 3vw, 1.4rem); }
  @media only screen and (max-width: 767px) {
    .tree_wrap .head_box {
      font-size: clamp(1rem, 5vw, 1.4rem); } }

.tree_wrap .m_label {
  font-size: clamp(1rem, 3vw, 1.4rem); }
  @media only screen and (max-width: 767px) {
    .tree_wrap .m_label {
      font-size: clamp(1rem, 5vw, 1.4rem); } }
  .tree_wrap .m_label.small_ {
    font-size: clamp(0.85rem, 4vw, 1.15rem); }
    @media only screen and (max-width: 767px) {
      .tree_wrap .m_label.small_ {
        font-size: clamp(0.85rem, 6vw, 1.2rem); } }

.tree_wrap .row_3 .box_:nth-child(1) .pos_top {
  animation: dash_anim_css 1s linear infinite; }

.tree_wrap .row_3 .box_:nth-child(2) .pos_top {
  animation: dash_anim_line_r_css 1s linear infinite; }

.traingle_wrap .t_img .numberbox .number_box {
  left: 0;
  right: auto; }

.traingle_wrap .label_box {
  right: 8%;
  left: auto;
  transform: rotate(53deg);
  transform-origin: right bottom; }
  @media only screen and (max-width: 980px) {
    .traingle_wrap .label_box {
      right: 3%; } }
